import { useEffect, useMemo, useState } from 'react';
import { useToast } from '../../components/ui/ToastProvider';

type OrderStatus = 'pending' | 'processing' | 'completed' | 'cancelled';

interface Order {
  _id: string;
  orderNumber: string;
  customerId: { name: string; email: string; phone: string } | null;
  vendorId: { _id: string; name: string } | null;
  items: Array<{
    productId: string;
    name: string;
    quantity: number;
    price: number;
    total: number;
  }>;
  pricing: {
    totalAmount: number;
  };
  status: OrderStatus;
  createdAt: string;
}

export default function VendorOrders() {
  const [orders, setOrders] = useState<Order[]>([]);
  const [dragOver, setDragOver] = useState<OrderStatus | null>(null);
  const [draggingId, setDraggingId] = useState<string | null>(null);
  const [draggingFrom, setDraggingFrom] = useState<OrderStatus | null>(null);
  const [loading, setLoading] = useState(true);
  const { pushToast } = useToast();

  // Get vendorId from localStorage (set during login)
  const vendorId = typeof window !== 'undefined' ? (localStorage.getItem('cc_vendorId') || localStorage.getItem('vendorId')) : null;

  useEffect(() => {
    if (vendorId) {
      loadOrders();
    } else {
      pushToast({ type: 'error', title: 'Error', message: 'Vendor not logged in' });
      setLoading(false);
    }
  }, [vendorId]);

  const loadOrders = async () => {
    try {
      console.log('[vendor:loadOrders] vendorId=', vendorId);
      const res = await fetch(`http://localhost:5000/api/orders?vendorId=${vendorId}&status=pending,processing,completed,cancelled`);
      if (!res.ok) throw new Error('Failed to fetch orders');
      const data = await res.json();
      console.log('[vendor:loadOrders] fetched orders count=', data.length);
      setOrders(data);
    } catch (error: any) {
      pushToast({ type: 'error', title: 'Error', message: error.message || 'Failed to load orders' });
    } finally {
      setLoading(false);
    }
  };

  const columns = useMemo(() => {
    const now = new Date();
    const startOfToday = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const endOfToday = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1);

    return {
      pending: orders.filter((o) => o.status === 'pending'),
      processing: orders.filter((o) => o.status === 'processing'),
      completed: orders.filter((o) => {
        if (o.status !== 'completed' && o.status !== 'cancelled') return false;
        const d = new Date(o.createdAt);
        return d >= startOfToday && d < endOfToday;
      }),
    };
  }, [orders]);

  const canDropTo = (from: OrderStatus | null, to: OrderStatus): boolean => {
    if (!from) return false;
    if (from === 'pending' && to === 'processing') return true;
    if (from === 'processing' && to === 'completed') return true;
    return false;
  };

  const updateOrderStatus = async (orderId: string, newStatus: OrderStatus) => {
    try {
      const res = await fetch(`http://localhost:5000/api/orders/${orderId}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ status: newStatus }),
      });

      if (!res.ok) throw new Error('Failed to update order');
      const updated = await res.json();
      console.log('[vendor:updateStatus] orderId=', orderId, 'status=', newStatus);
      setOrders((prev) => prev.map((o) => (o._id === orderId ? { ...o, status: updated.status } : o)));
      pushToast({ type: 'success', title: 'Order Updated', message: `Order moved to ${newStatus}` });
    } catch (error: any) {
      pushToast({ type: 'error', title: 'Update Failed', message: error.message || 'Could not update order' });
    }
  };

  const onDragStart = (e: React.DragEvent, id: string, from: OrderStatus) => {
    setDraggingId(id);
    setDraggingFrom(from);
    e.dataTransfer.setData('text/plain', id);
    e.dataTransfer.effectAllowed = 'move';
  };

  const onDragOver = (e: React.DragEvent, to: OrderStatus) => {
    if (!draggingFrom || !canDropTo(draggingFrom, to)) return;
    e.preventDefault();
    e.dataTransfer.dropEffect = 'move';
    setDragOver(to);
  };

  const onDragLeave = (_e: React.DragEvent, to: OrderStatus) => {
    if (dragOver === to) setDragOver(null);
  };

  const onDrop = (e: React.DragEvent, to: OrderStatus) => {
    e.preventDefault();
    const id = e.dataTransfer.getData('text/plain');
    setDragOver(null);
    setDraggingId(null);
    setDraggingFrom(null);

    if (!draggingFrom || !canDropTo(draggingFrom, to)) return;

    updateOrderStatus(id, to);
  };

  const advance = (id: string, currentStatus: OrderStatus) => {
    if (currentStatus === 'pending') {
      updateOrderStatus(id, 'processing');
    } else if (currentStatus === 'processing') {
      updateOrderStatus(id, 'completed');
    }
  };

  const cancelOrder = (id: string) => {
    updateOrderStatus(id, 'cancelled');
  };

  const rejectOrder = (id: string) => {
    updateOrderStatus(id, 'cancelled');
  };

  const Column = ({ title, type }: { title: string; type: OrderStatus }) => (
    <section
      className={`orders-column ${dragOver === type ? 'drag-over' : ''}`}
      onDragOver={(e) => onDragOver(e, type)}
      onDragLeave={(e) => onDragLeave(e, type)}
      onDrop={(e) => onDrop(e, type)}
      aria-label={`${title} orders`}
    >
      <header className="orders-column-header">
        <h2 className="orders-column-title">{title}</h2>
        <span className="orders-count">{columns[type].length}</span>
      </header>
      <div className="orders-column-body">
        {columns[type].map((o) => (
          <article
            key={o._id}
            className={`order-card ${draggingId === o._id ? 'dragging' : ''}`}
            draggable={type !== 'completed'}
            onDragStart={(e) => onDragStart(e, o._id, type)}
            aria-grabbed={draggingId === o._id}
          >
            <div className="order-row">
              <div className="order-id">{o.orderNumber || o._id}</div>
              <div className="order-total">â‚¹{o.pricing.totalAmount.toFixed(2)}</div>
            </div>
            <div className="order-customer">{o.customerId?.name || 'Unknown Customer'}</div>
            <div className="order-items">
              {o.items.map((item) => (
                <span
                  key={item.productId}
                  onClick={() => (window.location.href = `/vendor/product-sales/${item.productId}`)}
                  style={{ color: 'var(--primary)', cursor: 'pointer', textDecoration: 'underline' }}
                  title="View product sales detail"
                >
                  {item.name} x{item.quantity}
                </span>
              )).reduce((prev, curr) => [prev, ', ', curr])}
            </div>
            <div className="order-meta">{new Date(o.createdAt).toLocaleString()}</div>
            {(o.status === 'completed' || o.status === 'cancelled') ? (
              <div className="order-footer">
                <span className={`status-badge ${o.status === 'cancelled' ? 'cancelled' : 'done'}`}>
                  {o.status === 'cancelled' ? 'Cancelled' : 'Completed'}
                </span>
              </div>
            ) : (
              <div className="order-footer">
                {type === 'pending' && (
                  <div className="btn-group">
                    <button className="btn-small primary" onClick={() => advance(o._id, o.status)}>Accept</button>
                    <button className="btn-small danger" onClick={() => rejectOrder(o._id)}>Reject</button>
                  </div>
                )}
                {type === 'processing' && (
                  <div className="btn-group">
                    <button className="btn-small primary" onClick={() => advance(o._id, o.status)}>Done</button>
                    <button className="btn-small danger" onClick={() => cancelOrder(o._id)}>Cancel</button>
                  </div>
                )}
              </div>
            )}
          </article>
        ))}
      </div>
    </section>
  );

  if (loading) {
    return (
      <div className="orders-board">
        <div style={{ padding: '24px', textAlign: 'center', color: 'var(--text-secondary)' }}>
          Loading orders...
        </div>
      </div>
    );
  }

  return (
    <div className="orders-board" role="region" aria-label="Orders Kanban Board">
      <Column title="Pending" type="pending" />
      <Column title="Processing" type="processing" />
      <Column title="Final Status" type="completed" />
    </div>
  );
}
